.modalBackground {
  width: 100vw;
  height: 90vh;
  background-color: rgba(200, 200, 200);
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  overflow:scroll;
}

.modalContainer {
  width: 500px;
  max-height: 80vh; /* Set a maximum height for the modal container */
  border-radius: 12px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  display: flex;
  flex-direction: column;
  padding: 25px;
}

.modalContainer .title {
  display: inline-block;
  text-align: center;
  margin-top: 10px;
}

.titleCloseBtn {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

.titleCloseBtn button {
  background-color: transparent;
  border: none;
  font-size: 25px;
  cursor: pointer;
}

.modalContainer .body {
  flex: 1; /* Allow the body to grow and shrink as needed */
  overflow: auto;
}

.modalContainer .body form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.modalContainer .body label {
  margin-top: 10px;
}

.modalContainer .body input,
.modalContainer .body textarea,
.modalContainer .body select {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  box-sizing: border-box;
}

.modalContainer .body button {
  width: 100%;
  height: 45px;
  margin-top: 10px;
  border: none;
  background-color: cornflowerblue;
  color: white;
  border-radius: 8px;
  font-size: 20px;
  cursor: pointer;
}

.modalContainer .body button[type="button"] {
  background-color: lightgray;
}

#cancelBtn {
  background-color: crimson;
}

.modalContainer .footer {
  flex: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}
